import React, { useEffect, useState } from 'react';
import { useDispatch,  useSelector } from 'react-redux';
import {useHistory} from 'react-router-dom';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import ListItem from '@mui/material/ListItem';
import Avatar  from '@mui/material/Avatar';
import IconButton  from '@mui/material/IconButton';
import 'react-alice-carousel/lib/alice-carousel.css';

import TwitterIcon from '@mui/icons-material/Twitter';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';

import {page} from '../../styles/main';
import { List, requirePropFactory } from '@mui/material';



import { getData } from '../../actions/instrument';
import Bird from '../../components/birds/Bird';
import bird_data from '../../data/summary/BIRD_SUMMARY.json';


const classes = {
    root:{
        marginTop:10
    }
}

const styles = {
    media: {
      height: '100%',
      paddingTop: '75%', // 16:9,
      marginTop:'30'
    },
};






export default function Main(){
    const history = useHistory();
    const dispatch = useDispatch();
    const [bird, setBird] = useState("aecm");
    const [id, setId] = useState(0);

    function handleClick(dir){
      var newId;
      if(dir == "next"){
        newId = id + 1;
      }else{
        newId = id - 1;
      }
      setId(newId);
      const newBird = bird_data[newId].code.toLowerCase();
      setBird(newBird);
    }
    

    return(
      <div>
      <Grid style={{height:"100vh"}} justifyContent="center"   container direction="row">
        <Grid item xs={12} lg={8} sm={12} md={12} style={{marginTop:0, padding:10,}}>
           
                <Grid container direction="row" justifyContent="space-between">
                  <Grid item>
                  <Typography variant="h6"><b>THERMPA</b></Typography>
                  </Grid>
                  <Grid item underline="none">
                    <Button onClick={() => history.push("/about")}><Typography variant="body1"> About </Typography></Button>
                  </Grid>
                </Grid>
               
               <div style={{padding:10}}/>
               <Typography align="justify">
               This is a web-based simulator for predicting thermoregulatory responses of different species of birds. 
               Different machine-learning based models are presented for predicting body temperature, evaporative water loss and 
               resting metabolic rates under different ambient temperatures and body mass.
               </Typography>

               <div style={{padding:10}}/>

               <Grid container direction="row" justifyContent="space-between">
                <Grid item>
                <IconButton disabled={id == 0} onClick={() => handleClick("prev")} style={{borderColor:"GrayText", borderStyle:"solid", borderWidth:2}} variant="outlined"><ChevronLeftIcon /></IconButton>
                <IconButton  disabled={id == 36} onClick={() => handleClick("next")} style={{borderColor:"GrayText", borderStyle:"solid", borderWidth:2}} variant="outlined"><ChevronRightIcon /></IconButton>
                </Grid>
                <Grid item>
                <Button onClick={() => history.push("/birds")} variant='contained' size="large"><Typography>View All</Typography></Button>
                </Grid>
               </Grid>
               <div style={{display:"flex", justifyContent:"flex-end", flexDirection:"row"}}>

               
               </div>
               
               <Bird code={bird} />


               
             
                

                
        </Grid>
      </Grid>
           
        </div>
    )
}
function Display(props){
    const history = useHistory();
    const [data, setData] = useState(props.data ? props.data : [])

    useEffect(() => {
      setData(props.data ? props.data: []);
    }, [props.data])

    function handleClick(){
        history.push("/bird/" + props.data.code.toLowerCase());
    }


    return(
       
          <ListItem button onClick={handleClick}>
            <div style={{flex:1, flexDirection:"column", justifyContent:"center", alignItems:"center"}}>
            <Grid container direction="row" justifyContent="center">
                <Avatar src={require('../../images/birds/' + props.data.code + '.jpg')} style={{width:60, height:60}} >
                  <TwitterIcon />
                </Avatar>
                    
                </Grid>
               
                <Typography align="center" variant="body1"> 
                    {props.data.name} 
                </Typography>
            </div>
                
                
           
          </ListItem>
    )
}