import React, { useEffect, useState } from 'react';
import { useDispatch,  useSelector } from 'react-redux';
import {useHistory} from 'react-router-dom';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import ListItem from '@mui/material/ListItem';

import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import Avatar  from '@mui/material/Avatar';
import 'react-alice-carousel/lib/alice-carousel.css';

import TwitterIcon from '@mui/icons-material/Twitter';

import {page} from '../../styles/main';
import { IconButton, List, requirePropFactory } from '@mui/material';

import bird_data from '../../data/summary/BIRD_SUMMARY.json';


import { getData } from '../../actions/instrument';


const classes = {
    root:{
        marginTop:10
    }
}

const styles = {
    media: {
      height: '100%',
      paddingTop: '75%', // 16:9,
      marginTop:'30'
    },
};






export default function Main(){
    const history = useHistory();
    const dispatch = useDispatch();
    

    return(
      <div>
      <Grid style={{height:"100vh"}} justifyContent="center"  container direction="row">
        <Grid item xs={12} lg={8} sm={12} md={12} style={{marginTop:0, padding:10,}}>
           
              
               
               
                <div style={{marginTop:50}}>

                <IconButton onClick={() => history.goBack()} color="primary">
                  <ChevronLeftIcon fontSize="large" />
                </IconButton>

               

                <div style={{marginTop:20}}>

                <Grid container direction="row" justifyContent="center">
                <Grid item md={4}>
                <img src={require("../../images/Birds.jpg")} style={{width:500}} />
                </Grid>
               
                </Grid>
                
                <div style={{marginTop:20, marginBottom:20}}>
                <Typography variant="h6"><b>THERMPA - Thermoregulatory models for Passeriformes </b></Typography>
                </div>
                
                
                <Typography align="justify">
                Thermoregulation is an important bodily regulation function carried out by living organisms
                in adaptation or response to their environment. Living organisms can adjust their body 
                temperatures relative to their ambient temperature. Based on the temperature regulation mechanism,
                living things can be classified as homeotherms, poikilotherms, and heterotherms. 

                Passeriformes are endotherms that can raise and lower their body temperatures in response to their 
                environment. THERMPA is an ML-based tool for simulating the thermoregulatory properties of different Passeriformes 
                under varying environmental temperatures. The simulator consists of 37 birds, of which 17 are from the montane habitat 
                and 20 are from lowland habitats. 

                Four different ML-based models including support vector regression (SVR), gaussian process regression (GPR), 
                artificial neural network (ANN) and Ensemble Learners (EL) are available in THERPMA to 
                simulate body temperature (Ta), evaporative water loss (EWL g/h), and resting metabolic rate (RMR, W) 
                of the different birds.
                </Typography>
                </div>
              
                

                </div>
                

                
        </Grid>
      </Grid>
           
        </div>
    )
}
function Display(props){
    const history = useHistory();
    const [data, setData] = useState(props.data ? props.data : [])

    useEffect(() => {
      setData(props.data ? props.data: []);
    }, [props.data])

    function handleClick(){
        history.push("/bird/" + props.data.code.toLowerCase());
    }


    return(
       
          <ListItem button onClick={handleClick}>
            <div style={{flex:1, flexDirection:"column", justifyContent:"center", alignItems:"center"}}>
            <Grid container direction="row" justifyContent="center">
                <Avatar src={require('../../images/birds/' + props.data.code + '.jpg')} style={{width:60, height:60}} >
                  <TwitterIcon />
                </Avatar>
                    
                </Grid>
               
                <Typography align="center" variant="body1"> 
                    {props.data.name} 
                </Typography>
            </div>
                
                
           
          </ListItem>
    )
}