function MassSlider(props){
    const [mass, setMass] = useState(props.min);

    useEffect(() => {
        setMass(props.min)
    }, [props.min])

    function valuetext(value) {
        return `${value}C`;
    }

    const handleChange = (event, newValue) => {
        setMass(newValue);
        props.onChange(newValue)
    };

    function getMarks(){
        const marks = [
            {
              value: props.min,
              label: props.min + 'g',
            },
            {
              value: ((props.max+props.min)/2).toFixed(1),
              label: ((props.max+props.min)/2).toFixed(1) + 'g',
            },
            {
              value: props.max,
              label: props.max + 'g',
            },
          ];
        return marks
    }
   
    return(
        <div style={{padding:10}}>
            <Grid container direction="row" justifyContent="space-between">
                    <Grid item>
                    <Typography variant="body1"><b>Body Mass</b></Typography>
                    </Grid>
                    <Grid item>
                    <Typography variant="h6" style={{fontFamily:"Space Grotesk"}} >{mass} g</Typography>
                    </Grid>
                    
                    </Grid>
                   
                    <Slider
                        aria-label="Custom marks"
                        defaultValue={props.min}
                        onChange={handleChange}
                        max={props.max}
                        min={props.min}
                        disabled={!props.enable}
                        step={0.1}
                        valueLabelDisplay="auto"
                        marks={getMarks()}
                        getAriaValueText={valuetext}
                    />
        </div>
    )
}

function TempSlider(props){
    const [temp, setTemp] = useState(20);

    const handleChange = (event, newValue) => {
        setTemp(newValue);
        props.onChange(newValue);
    };

    function valuetext(value) {
        return `${value}C`;
    }
    const marks = [
        {
          value: 0,
          label: '0C',
        },
        {
          value: 20,
          label: '20C',
        },
        {
          value: 40,
          label: '40C',
        },
        {
          value: 50,
          label: '50C',
        },
      ];
    return(
        <div style={{padding:10}}>
            <Grid container direction="row" justifyContent="space-between">
                    <Grid item>
                    <Typography variant="body1"><b>Ambient Temperature</b></Typography>
                    </Grid>
                    <Grid item>
                    <Typography variant="h6" style={{fontFamily:"Space Grotesk"}} >{temp} &#xb0;C</Typography>
                    </Grid>
                    </Grid>
                    <Slider
                        aria-label="Custom marks"
                        defaultValue={temp}
                        onChange={handleChange}
                        max={50}
                        disabled={!props.enable}
                        getAriaValueText={valuetext}
                        step={0.1}
                        valueLabelDisplay="auto"
                        marks={marks}
                    />
        </div>
    )
}